// router/index.js

import modules from './modules'
import Vue from 'vue'
//npm
import Router from 'uni-simple-router'

Vue.use(Router)
//
const router = new Router({
    routes: [...modules]//
});

//
router.beforeEach((to, from, next) => {
	if(!to.name){
		router.push({
			name: 'index'
		});
	}
	  next()
})
// 
router.afterEach((to, from) => {
})
export default router;